.form-field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #d2d2d2;
  outline: 0;
  font-size: 16px;
  color: #212121;
  padding: 7px 0;
  background: transparent;
  -webkit-transition: border-color 0.2s;
  transition: border-color 0.2s;
}

.form-field::-webkit-input-placeholder {
  color: transparent;
}

.form-field::-moz-placeholder {
  color: transparent;
}

.form-field::-ms-input-placeholder {
  color: transparent;
}

.form-field::placeholder {
  color: transparent;
}

.form-field:placeholder-shown ~ .form-label {
  font-size: 16px;
  cursor: text;
  top: 20px;
}

label,
.form-field:focus ~ .form-label {
  position: absolute;
  top: 0;
  display: block;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 12px;
  color: #9b9b9b;
}

.form-field:focus ~ .form-label {
  color: #009788;
}

.form-field:focus {
  padding-bottom: 6px;
  border-bottom: 2px solid #009788;
}


/*button*/
.exit {
  cursor: pointer;
  top:25px;
  left:40px;
  display: inline-block;
  padding: 12px;
  border: 1px solid gray;
  border-radius: 4px;
  width: auto;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  position: absolute;
  overflow: hidden;
}
.exit:before {
  content: "";
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%)scaleY(1)scaleX(1.25);
          transform: translateX(-50%)scaleY(1)scaleX(1.25);
  top: 100%;
  width: 100%;
  height: 180%;
  background-color: black;
  border-radius: 50%;
  display: block;
  -webkit-transition: all 0.5s 0.1s cubic-bezier(0.55,0,0.1,1);
  transition: all 0.5s 0.1s cubic-bezier(0.55,0,0.1,1);
  z-index: -1;
}

.exit:after {
  content: "";
  position: absolute;
  left: 55%;
  -webkit-transform: translateX(-50%)scaleY(1)scaleX(1.45);
          transform: translateX(-50%)scaleY(1)scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: red;
  border-radius: 50%;
  display: block;
  -webkit-transition: all 0.5s 0.1s cubic-bezier(0.55,0,0.1,1);
  transition: all 0.5s 0.1s cubic-bezier(0.55,0,0.1,1);
  z-index: -1;
}

.exit:hover {
 color: white;
 border: 1px solid red;
}

.exit:hover:before {
  top: -35%;
  background-color: red;
  -webkit-transform: translateX(-50%)scaleY(1.3)scaleX(0.8);
          transform: translateX(-50%)scaleY(1.3)scaleX(0.8);
}

.exit:hover:after {
  top: -45%;
  background-color: red;
  -webkit-transform: translateX(-50%)scaleY(1.3)scaleX(0.8);
          transform: translateX(-50%)scaleY(1.3)scaleX(0.8);
}

